.container {
  background-color: #d8c75f;
  width: 100%;
  height: 100%; }

.choice-text {
  font-family: 'Rubik One', sans-serif;
  font-size: 1.5em; }

.img-container {
  padding-top: 120px; }
  @media screen and (max-width: 500px) {
    .img-container {
      padding-top: 30px; } }

.option-img {
  height: 100px;
  width: 100px;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
  cursor: pointer;
  margin-right: 60px; }
  .option-img:hover {
    -webkit-transform: scale(1.5, 1.5);
        -ms-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5); }
  @media screen and (max-width: 500px) {
    .option-img {
      width: 80px;
      height: 80px;
      margin-right: 20px; } }



