.App {
  text-align: center;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background: #e53935;
  background: -webkit-gradient(linear, right top, left top, from(#e53935), to(#e35d5b));
  background: -webkit-linear-gradient(right, #e53935, #e35d5b);
  background: -o-linear-gradient(right, #e53935, #e35d5b);
  background: linear-gradient(to left, #e53935, #e35d5b);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



