.container {
  background-color: #d8c75f;
  width: 100%;
  height: 100%; }

.card {
  margin-right: 20px;
  border: 3px solid #ffff;
  border-radius: 4px;
  width: 40%;
  margin: 3vh;
  height: 50%;
  background-color: #9ccc65;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  float: left; }
  .card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }
  @media screen and (max-width: 500px) {
    .card {
      width: 40%;
      margin: 10px; } }

.card-image {
  height: 100px;
  width: 100px; }
  @media screen and (max-width: 500px) {
    .card-image {
      height: 50px;
      width: 50px; } }

.card-container {
  padding: 2px 16px; }

.win-text {
  clear: both;
  padding: 10px;
  font-size: 1.5em; }



